import login from '../factory/login.js'


export default{
	/**
	 * 
	 * @param {Object} _reqObj 
	 */
	request:function(_reqObj) {
		//
		login.checkLoginStatus(function() {
			//token
			_reqObj.header.cookie = '_java110_token_=' + uni.getStorageSync('token'); //console.log("_reqObj",_reqObj);
			uni.request(_reqObj);
		});
	}
}