import url from '../../constant/url.js';

/**
 * 
 * @param {Object} _that 
 * @param {Object} _data 
 */
export function loadTodoCompaint(_that,_data){
	return new Promise(function(reslove,reject){
		_that.context.get({
			url: url.listAuditComplaints,
			data:_data,
			success: function(res) {
				reslove(res);
			},
			fail: function(e) {
				wx.showToast({
					title: "",
					icon: 'none',
					duration: 2000
				})
			}
		})
	});
}

